import React, { useState } from 'react'

const Form = () => {
    const [pageNo, setpageNo] = useState(0);
    const [selectAnswer, setselectAnswer] = useState("")
    const [score, setscore] = useState(0);
    const questionArray = [
        {
            'Qno': 0,
            'Question': 'Q1.What is the DOB of shree?',
            'option1': '3 sept',
            'option2': '2 sept',
            'option3': '4 sept',
            'option4': '1 sept',
            'answer': 'option2'
        },
        {
            'Qno': 1,
            'Question': 'Q2.What is the Role of shree In company?',
            'option1': 'Frontend developer',
            'option2': 'Backend developer',
            'option3': 'fullstack developer',
            'option4': 'deveops Engineer',
            'answer': 'option3'
        },
        {
            'Qno': 2,
            'Question': 'Q3.Who was the whole four year roomate of shree?',
            'option1': 'prajwal',
            'option2': 'Suyog',
            'option3': 'Ningappa',
            'option4': 'None ',
            'answer': 'option4'
        },
        {
            'Qno': 3,
            'Question': 'Q4.Who was the whole four year roomate of shree?',
            'option1': 'prajwal',
            'option2': 'Suyog',
            'option3': 'Ningappa',
            'option4': 'None ',
            'answer': 'option4'
        },
        {
            'Qno': 4,
            'Question': 'Q5.Who was the whole four year roomate of shree?',
            'option1': 'prajwal',
            'option2': 'Suyog',
            'option3': 'Ningappa',
            'option4': 'None ',
            'answer': 'option4'
        },
        {
            'Qno': 5,
            'Question': 'Q6.Who was the whole four year roomate of shree?',
            'option1': 'prajwal',
            'option2': 'Suyog',
            'option3': 'Ningappa',
            'option4': 'None ',
            'answer': 'option4'
        },
        {
            'Qno': 6,
            'Question': 'Q7.Who was the whole four year roomate of shree?',
            'option1': 'prajwal',
            'option2': 'Suyog',
            'option3': 'Ningappa',
            'option4': 'None ',
            'answer': 'option4'
        },
        {
            'Qno': 7,
            'Question': 'Q8.Who was the whole four year roomate of shree?',
            'option1': 'prajwal',
            'option2': 'Suyog',
            'option3': 'Ningappa',
            'option4': 'None ',
            'answer': 'option4'
        },
        {
            'Qno': 8,
            'Question': 'Q9.Who was the whole four year roomate of shree?',
            'option1': 'prajwal',
            'option2': 'Suyog',
            'option3': 'Ningappa',
            'option4': 'None ',
            'answer': 'option4'
        },
        {
            'Qno': 9,
            'Question': 'Q10.Who was the whole four year roomate of shree?',
            'option1': 'prajwal',
            'option2': 'Suyog',
            'option3': 'Ningappa',
            'option4': 'None ',
            'answer': 'option4'
        },
    ]
    const onclick = () => {
        if (questionArray[pageNo].answer === selectAnswer.selectedAnswer) {
            setscore(score + 1);
            console.log('if block', score)
        }
        setpageNo(pageNo + 1);
        console.log(score, pageNo)
    }
    const onchange = (e) => {
        setselectAnswer({ ...selectAnswer, [e.target.name]: e.target.value })
    }

    return (
        <div>
            {(pageNo <= 9) ?


                <div className="card">
                    <div className="card-body">
                        <h5 className="card-title">{questionArray[pageNo].Question}</h5>
                        <br />
                        <div className="row">
                            <div className='col'>
                                <div className="form-check">
                                    <input className="form-check-input" type="radio" name="selectedAnswer" id="exampleRadios1" value="option1" onChange={onchange} checked={1 === 2}/>
                                    <label className="form-check-label" htmlFor="exampleRadios1">
                                        {questionArray[pageNo].option1}
                                    </label>
                                </div>
                            </div>
                            <div className='col'>
                                <div className="form-check">
                                    <input className="form-check-input" type="radio" name="selectedAnswer" id="exampleRadios2" value="option2" onChange={onchange} checked={1 === 2}/>
                                    <label className="form-check-label" htmlFor="exampleRadios2">
                                        {questionArray[pageNo].option2}
                                    </label>
                                </div>
                            </div>
                        </div>
                        <br />
                        <div className="row">
                            <div className='col'>
                                <div className="form-check">
                                    <input className="form-check-input" type="radio" name="selectedAnswer" id="exampleRadios3" value="option3" onChange={onchange} checked={1 === 2}/>
                                    <label className="form-check-label" htmlFor="exampleRadios3">
                                        {questionArray[pageNo].option3}
                                    </label>
                                </div>
                            </div>
                            <div className='col'>
                                <div className="form-check">
                                    <input className="form-check-input" type="radio" name="selectedAnswer" id="exampleRadios4" value="option4" onChange={onchange} checked={1 === 2}/>
                                    <label className="form-check-label" htmlFor="exampleRadios4">
                                        {questionArray[pageNo].option4}
                                    </label>
                                </div>
                            </div>
                        </div>
                        <br />
                        <div className="d-grid gap-2 col-6 mx-auto">
                            <button className="btn btn-info" type="button" onClick={onclick}> Next</button>
                        </div>
                    </div>
                </div>
                : <div className="card">
                    <div className="card-body">
                        <h5 className="card-title">
                            Your Score {score}/10 
                            {localStorage.setItem('shreescore',1) }
                        </h5>
                        <p>take the screen shot and send it to shree</p>
                    </div>
                </div>}
        </div>
    )
}

export default Form